<template lang="html">
  <div class="row">
    <div class="card mx-auto">
      <div class="card-header ">
        <h4>Register Employee</h4>
      </div>
      <div class="validation">
        {{validation}}
      </div>
      <div class="card-body">
        <form @submit.prevent="registerUser">
          <div class="form-group">
            <label for="username">Username</label>
            <input
              class="form-control"
              type="text"
              placeholder="Username"
              name="username"
              v-model="username"
              id="username"
              >
          </div>
          <div class="form-group">
            <label for="Name">Name</label>
            <input
              class="form-control"
              type="text"
              placeholder="name"
              name="name"
              v-model="name"
              id="name"
              >
          </div>
          <div class="form-group">
            <label for="Email">Email</label>
            <input
              class="form-control"
              type="text"
              placeholder="Email"
              name="email"
              v-model="email"
              id="email"
              >
          </div>
          <div class="form-group">
            <label for="password">Password</label>
            <input
              class="form-control"
              type="password"
              placeholder="Password"
              name="password"
              v-model="password"
              id="password"
              >
          </div>
          <div class="form-group">
            <label for="confirm_password">Confirm password</label>
            <input
              class="form-control"
              type="password"
              placeholder="Confirm password"
              name="confirm_password"
              v-model="confirm_password"
              id="confirm_password"
              >
          </div>

          <div class="group" >
            <label for="companys">Kompanija: </label>
            <select name="type" id="type" v-model="type" @change="onChangeCompany($event)">
               <option  v-for="company in companys" v-bind:key="company.name">{{company.name}}</option>
            </select>
          </div>
          <div class="group" >
            <label for="companys">Sektor: </label>
            <select name="type" id="type" v-model="sector" @change="onChangeSector($event)">
               <option  v-for="sek in sektori" v-bind:key="sek">{{sek}}</option>
            </select>
          </div>
          <div class="group" >
            <label for="companys">Radno Mesto:</label>
            <select name="type" id="type" v-model="radnoMesto" >
               <option  v-for="rm in radnaMesta" v-bind:key="rm">{{rm}}</option>
            </select>
          </div>
          <button class="btn btn-primary">Register</button>


        </form>

      </div>
    </div>
  </div>
</template>

<script>

import {mapActions,mapGetters} from 'vuex';
export default {
  data(){
    return{
      username:"",
      password:"",
      confirm_password:"",
      name:"",
      email:"",
      type:"",
      radnoMesto:"",
      sektor:"",
      sektori:[],
      radnaMesta:[],
      validation:"",
    }
  },
  computed: mapGetters(['companys','users']),
  methods:{
    ...mapActions(['register','getCompanies','getAllUsers']),
    registerUser(){
      // eslint-disable-next-line no-unused-vars
      this.validation="";
      let user = {
        username: this.username,
        password: this.password,
        confirm_password: this.confirm_password,
        email: this.email,
        name:this.name,
        type:this.type,
        radnoMesto:this.radnoMesto,
        sektor:this.sector
      }
      for (var i = 0; i < this.users.length; i++) {
        if(this.users[i].username == user.username){
          this.validation="Username je već registrovan. Izaberite drugačiji username."
        }
      }
      if(this.validation==""){
        for ( i = 0; i < this.users.length; i++) {
          if(this.users[i].email == user.email){
            this.validation="Email je već registrovan. Izaberite drugačiji Email."
          }
        }
      }
      if(this.validation==""){

        if(user.username==""){
          this.validation="Popunite sva polja prilikom registracije."
        }
        if(user.password==""){
          this.validation="Popunite sva polja prilikom registracije."
        }
        if(user.confirm_password==""){
          this.validation="Popunite sva polja prilikom registracije."
        }
        if(user.email==""){
          this.validation="Popunite sva polja prilikom registracije."
        }
        if(user.name==""){
          this.validation="Popunite sva polja prilikom registracije."
        }
        if(user.type==""){
          this.validation="Popunite sva polja prilikom registracije."
        }
        if(user.radnoMesto==""){
          this.validation="Popunite sva polja prilikom registracije."
        }
        if(user.sektor==""){
          this.validation="Popunite sva polja prilikom registracije."
        }
      }
      if(this.validation==""){
      this.register(user).then(res=>{
        if(res.data.success){
          // this.$router.push("login");
          this.validation=`Uspešno registrovan zaposleni ${user.name}`;
        }
      });
    }
    },
    onChangeCompany(event) {

        this.sektori = [];

        let tempCompany;
        for (var i = 0; i < this.companys.length; i++) {
          if(this.companys[i].name==event.target.value){
            tempCompany=this.companys[i];
          }
        }
        for ( i = 0; i < tempCompany.sektori.length; i++) {
          this.sektori.push(tempCompany.sektori[i].name);
        }

    },
    onChangeSector(event) {

        this.radnaMesta = [];

        let tempCompany;
        for (var i = 0; i < this.companys.length; i++) {
          if(this.companys[i].name==this.type){
            tempCompany=this.companys[i];
          }
        }

        let tempSector;
        for ( i = 0; i < tempCompany.sektori.length; i++) {
          if(tempCompany.sektori[i].name == event.target.value){
            tempSector=tempCompany.sektori[i];
          }
        }
        for ( i = 0; i < tempSector.radnaMesta.length; i++) {
          this.radnaMesta.push(tempSector.radnaMesta[i]);
        }

    }

  },
  created(){
    this.getCompanies();
  }
}
</script>

<style lang="css" scoped>

.row{

  padding-top: 5%;
  text-align: center;
  width:100%;


}
.card{
  width: 60%;
  border-radius: 0;
}
.btn{
  width: 60%;
  background-color: #003366;
  color: white;
  border-radius: 10px;
}
.form-group{
padding-top: 10px;
}
.form-control{
  border-radius: 0;
}
.card-header{
  background-color: #003366;
  color: white;
  border-radius: 10px;
}
.validation{
  margin-top: 5px;
  background-color: #00aaaa;
}
</style>
